/*
Note: any .css or .scss files included in the 'styles' directory
will be correctly compiled during `gulp serve` and `gulp`
 */

 @import url('https://fonts.googleapis.com/css?family=Arimo');

html, body {
  font-family: 'Arimo', 'Helvetica', sans-serif;
  /* font-size: 14px; */
  margin: 0;
  padding: 0;
}

header > h1 {
  font-size: 1.5em;
  text-align: center;
}

header a {
  color: #3186ad;
  text-decoration: none;
}

header > div {
  float: right;
  margin-top: -34px;
  margin-right: .5em;
}

#caspio_header {
  color: #444;
}

#caspio_header_logo {
  height: 24px;
  position: relative;
  top: 4px;
  margin: 0 .5em;
}

/* Center all Forms */
#caspioform > table {
  margin: auto;
}

a.button {
  display: inline-block;
  background-color: #F25824;
  font-size: 12px;
  color: #fff;
  padding: .7em 1em;
  text-decoration: none;
  margin-bottom: 1em;
}

/* PopUp */
.overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.7);
  transition: opacity 500ms;
  visibility: hidden;
  opacity: 0;
}
.overlay:target {
  visibility: visible;
  opacity: 1;
}

.popup {
  margin: 30px auto;
  padding: 1em;
  background: #fff;
  height: 600px;
  width: 650px;
  position: relative;
  transition: all 5s ease-in-out;
}

.popup h4 {
  margin-top: 0;
  color: #333;
  font-weight: normal;
  font-family: Arial, sans-serif;
  text-transform: uppercase;
  text-align: center;
}
.popup .close {
  position: absolute;
  top: .2em;
  right: .5em;
  transition: all 200ms;
  font-size: 30px;
  font-weight: normal;
  text-decoration: none;
  color: #474e53;
}
.popup .close:hover {
  color: rgb(49, 134, 173);
}
.popup .content {
  height: 555px;
  overflow: auto;
}

@media screen and (max-width: 700px){
  .box{
    width: 70%;
  }
  .popup{
    width: 70%;
  }
}
